import { cloneDeep, times, uniqBy } from "lodash/fp"

// Types and interfaces
type DimensionKey = string

interface Dimension {
  key: DimensionKey
}

type DimensionValue = Dimension & {
  value: string
}

type DimensionWithMetadata = Dimension & {
  meta?: { [key: string]: any }
}

interface Query {
  rowHeaders: DimensionWithMetadata[]
  columnHeaders: DimensionWithMetadata[]
  columns: DimensionKey[][]
  rows: DimensionKey[][]
  data: Value[][]
}

type PredicateInclude = Dimension & {
  include: string[]
  exclude: never
}

type PredicateExclude = Dimension & {
  include: never
  exclude: string[]
}

type Predicate = PredicateInclude | PredicateExclude

interface Column {
  x(): DimensionKey[]
  cells(): Cell[]
}

interface Row {
  y(): DimensionKey[]
  cells(): Cell[]
}

interface Cell {
  x(): DimensionKey[]
  y(): DimensionKey[]
  value(): Value
}

type Value = any

interface SliceOptions {
  x: Predicate[],
  y: Predicate[]
}

interface ReduceOptions {
  x: DimensionKey[],
  y: DimensionKey[],
  merge: (data: MultidimensionalDataset) => Value
}

interface Dataset {
  slice: (options: SliceOptions) => MultidimensionalDataset
  reduce: (options: ReduceOptions) => MultidimensionalDataset
  rowHeaders: () => DimensionWithMetadata[]
  columnHeaders: () => DimensionWithMetadata[]
  rows: () => Row[]
  columns: () => Column[]
  serialize: () => Query
}

class MultidimensionalDataset implements Dataset {
  private queryResult: Query

  constructor(queryResult: Query) {
    this.queryResult = queryResult
  }

  private sliceRows(queryResult: Query, options: Predicate[]): Query {
    return options.reduce((memo: Query, option: Predicate) => {
      const index = memo.rowHeaders.findIndex(header => header.key === option.key)
      const rowsToKeep: number[] = []
      memo.rows = memo.rows.filter((row: DimensionKey[], i: number) => {
        const keepRow = option.include !== undefined
          ? option.include.indexOf(row[index]) > -1
          : option.exclude.indexOf(row[index]) === -1
        if (keepRow) {
          rowsToKeep.push(i)
        }
        return keepRow
      })
      memo.data = memo.data.filter((row: Value[], i: number) => rowsToKeep.indexOf(i) > -1)
      return memo
    }, queryResult)
  }

  private sliceColumns(queryResult: Query, options: Predicate[]): Query {
    return options.reduce((memo: Query, option: Predicate) => {
      const index = memo.columnHeaders.findIndex(header => header.key === option.key)
      const columnsToKeep: number[] = []
      memo.columns = memo.columns.filter((column: DimensionKey[], i: number) => {
        const keepColumn = option.include !== undefined
          ? option.include.indexOf(column[index]) > -1
          : option.exclude.indexOf(column[index]) === -1
        if (keepColumn) {
          columnsToKeep.push(i)
        }
        return keepColumn
      })
      memo.data = memo.data.map(row => {
        return row.filter((cell: Value, i: number) => columnsToKeep.indexOf(i) > -1)
      })
      return memo
    }, queryResult)
  }

  // Returns a subset of a dataset
  slice(options: SliceOptions): MultidimensionalDataset {
    const slicedResult = this.sliceColumns(
      this.sliceRows(cloneDeep(this.queryResult), options.y),
      options.x
    )
    return new MultidimensionalDataset(slicedResult)
  }

  // Reduces the cells in a dataset
  reduce(options: ReduceOptions): MultidimensionalDataset {
    const rowIndices = options.y.map(key => this.queryResult.rowHeaders.findIndex(header => header.key === key))
    const rowGroups = uniqBy(arr => arr.join("/"))(this.queryResult.rows.map(row => rowIndices.map(i => row[i])))
    const columnIndices = options.x.map(key => this.queryResult.columnHeaders.findIndex(header => header.key === key))
    const columnGroups = uniqBy(arr => arr.join("/"))(this.queryResult.columns.map(column => columnIndices.map(i => column[i])))
    const data = [times((): Cell[] => [])(rowGroups.length)]
    rowGroups.forEach((rowGroup: any, rowIndex: number) => {
      columnGroups.forEach((columnGroup: any, columnIndex: number) => {
        const cell = this.slice({
          x: options.x.map((key, i) => {
            return { key, include: columnGroup[i] }
          }),
          y: options.y.map((key, i) => {
            return { key, include: rowGroup[i] }
          }),
        })
        data[rowIndex][columnIndex] = options.merge(cell)
      })
    })
    // Create new instance of dataset with processed result
    const result = cloneDeep(this.queryResult)
    result.data = data
    result.rows = rowGroups
    result.columns = columnGroups
    result.rowHeaders = result.rowHeaders.filter(header => options.y.indexOf(header.key) > -1)
    result.columnHeaders = result.columnHeaders.filter(header => options.x.indexOf(header.key) > -1)
    return new MultidimensionalDataset(result)
  }

  // Returns all columns
  columns(): Column[] {
    return this.queryResult.columns.map((column: DimensionKey[], j: number) => {
      return {
        x: () => column,
        cells: () => this.queryResult.data.map((row, i) => {
          return {
            x: () => column,
            y: () => this.queryResult.rows[i],
            value: () => row[j]
          }
        })
      }
    })
  }

  // Returns all rows
  rows(): Row[] {
    return this.queryResult.rows.map((row: DimensionKey[], i: number) => {
      return {
        y: () => row,
        cells: () => this.queryResult.data[i].map((cell, j) => {
          return {
            x: () => this.queryResult.columns[j],
            y: () => row,
            value: () => cell
          }
        })
      }
    })
  }

  rowHeaders(): DimensionWithMetadata[] {
    return this.queryResult.rowHeaders
  }

  columnHeaders(): DimensionWithMetadata[] {
    return this.queryResult.columnHeaders
  }

  serialize(): Query {
    return this.queryResult
  }
}

export default MultidimensionalDataset
