import * as React from "react"
import VisualizationWrapper from "./VisualizationWrapper"
import { Chart } from "../index"

import theme from "./constants"
import { defaults, filter, forEach, groupBy, indexOf, map, mapValues, pluck, reduce, sortBy, uniqBy, pickBy, identity } from "lodash/fp"
import { isArray } from "util";

const columnWidth: number = 150
const lineHeight: number = 30

export interface Props {
  data?: any
  height?: number
  width?: number
  facade?: any
  config?: any
  accessors?: any
  rowTitleOrientation?: "horizontal" | "vertical"
  rowLabelOrientation?: "horizontal" | "vertical"
  displayCellBorders?: boolean
}

const headerStyle = {
  lineHeight: `${lineHeight}px`,
  textAlign: "center",
  border: `1px solid ${theme.colors.axis.rules}`,
  overflow: "hidden",
  textOverflow: "ellipsis",
  padding: `${theme.space.small}px 0`
}

const titleStyle = {
  backgroundColor: theme.colors.background.lighter,
  ...headerStyle
}

const horizontalRowTitleStyle = {
  width: columnWidth,
  height: lineHeight,
  ...titleStyle
}

const verticalRowTitleStyle = (height: number) => {
  return {
    width: lineHeight,
    height: height,
    ...titleStyle
  }
}

const columnTitleStyle = (width: number) => {
  return {
    width: width,
    height: lineHeight,
    ...titleStyle
  }
}

const labelStyle = {
  backgroundColor: theme.colors.white,
  ...headerStyle
}

const horizontalRowLabelStyle = (height: number) => {
  return {
    height: height,
    width: columnWidth,
    ...labelStyle,
    textAlign: "end",
    paddingRight: 8,
  }
}

const verticalRowLabelStyle = (height: number) => {
  return {
    height: height,
    width: lineHeight,
    ...labelStyle,
  }
}

const columnLabelStyle = (width: number) => {
  return {
    height: lineHeight,
    width: width,
    float: "left",
    ...labelStyle
  }
}

const verticalTextStyle = (height: number) => {
  return {
    width: height,
    height: lineHeight,
    transform: `rotate(-90deg) translate(${lineHeight / 2}px, -${height}px)`,
    transformOrigin: "right top",
    whiteSpace: "nowrap",
    overflow: "hidden",
    textOverflow: "ellipsis",
    padding: `0 ${theme.space.small}px`
  }
}

const cellStyle = (displayBorder: boolean, width: number, height: number) => {
  return {
    height: height,
    width: width,
    border: `1px solid ${displayBorder ? theme.colors.axis.rules : theme.colors.white}`,
    float: "left",
    lineHeight: `${lineHeight}px`,
    textAlign: "end",
    paddingRight: 8,
  }
}

const defaultVisualizationConfig = (width: number, height: number) => {
  return {
    width,
    height,
    legend: false,
  }
}

class GridLayout extends React.Component<Props, {}> {
  columns: any = []
  rows: any = []
  cellHeight: number
  cellWidth: number
  rowTitleOrientation: "horizontal" | "vertical"

  public static defaultProps: Partial<Props> = {
    rowTitleOrientation: "horizontal",
    rowLabelOrientation: "horizontal",
    displayCellBorders: true,
  }

  computeCellDimensions() {
    this.cellHeight = this.props.height
      ? (this.props.height - this.getColumnsHeight()) / this.props.data.cells.length
      : lineHeight

    this.cellWidth = this.props.width
      ? (this.props.width - this.getRowsWidth()) / this.props.data.cells[0].length
      : columnWidth
  }

  computeColumns() {
    this.computeNestedColumns(this.props.data.nestedColumns)
    this.removeDuplicateTitles(this.columns)
    this.columns = sortBy(["level", "position"])(this.columns)
  }

  computeNestedColumns(columns: any, level: number = 0) {
    const maxPositionAtLevel: number = Math.max(Math.max(...pluck("position")(filter({ level })(this.columns))), -1)
    columns.forEach((column: any, j: number) => {
      column.nestedColumns && this.computeNestedColumns(column.nestedColumns, level + 1)
      column.level = level
      column.position = maxPositionAtLevel + 1 + j
      column.width = column.nestedColumns ? column.nestedColumns.reduce((width: number, col: any) => {
        return width + col.width
      }, 0) : 1
      delete column.nestedColumns
      this.columns.push(column)
    })
  }

  removeDuplicateTitles(array: any[]) {
    const groupedDimensions = groupBy("key")(filter({ kind: "dimension" })(array))
    forEach((group: any) => {
      if (group.length === 1) { return }
      const width = reduce((sum: number, dimension: any) => sum + (dimension.width || dimension.height), 0)(group)
      group.forEach((dimension: any, i: number) => {
        if (i === 0) {
          dimension.position = -1
          dimension.width ? dimension.width = width : dimension.height = width
        } else {
          array.splice(indexOf(dimension)(array), 1)
        }
      })
    })(groupedDimensions)
  }

  computeRows() {
    this.computeNestedRows(this.props.data.nestedRows)
    this.removeDuplicateTitles(this.rows)
    this.rows = sortBy(["level", "position"])(this.rows)
  }

  computeNestedRows(rows: any, level: number = 0) {
    const maxPositionAtLevel: number = Math.max(Math.max(...pluck("position")(filter({ level })(this.rows))), -1)
    rows.forEach((row: any, j: number) => {
      row.nestedRows && this.computeNestedRows(row.nestedRows, level + ((row.kind === "dimension" && this.rowTitleOrientation === "horizontal") ? 0 : 1))
      row.level = level
      row.position = (row.kind === "dimension") ? -1 : (maxPositionAtLevel + 1 + j)
      row.height = row.nestedRows ? row.nestedRows.reduce((height: number, col: any) => {
        return height + col.height
      }, 0) : 1
      delete row.nestedRows
      this.rows.push(row)
    })
  }

  getColumnsWidth() {
    const maxPosition = Math.max(...pluck("position")(this.columns))
    return (maxPosition + 1) * this.cellWidth
  }

  getRowsWidth() {
    const numberOfLevels = Math.max(...pluck("level")(this.rows)) + 1
    const numberOfVerticalLevels = filter((row: any) => {
      return (this.rowTitleOrientation === "vertical" && row.kind === "dimension") || (this.props.rowLabelOrientation === "vertical" && row.kind !== "dimension")
    })(uniqBy("level")(this.rows)).length
    return numberOfVerticalLevels * lineHeight + (numberOfLevels - numberOfVerticalLevels) * columnWidth
  }

  getColumnsHeight() {
    const maxPosition = Math.max(...pluck("level")(this.columns))
    return (maxPosition + 1) * lineHeight
  }

  renderRows() {
    const style = (row: any) => {
      return row.kind === "dimension"
        ? this.rowTitleOrientation === "horizontal" ? horizontalRowTitleStyle : verticalRowTitleStyle(row.height * this.cellHeight)
        : (this.props.rowLabelOrientation === "horizontal" ? horizontalRowLabelStyle : verticalRowLabelStyle)(row.height * this.cellHeight)
    }

    return (
      map((rowsInLevel: any) => {
        return <div style={{float:"left"}}>
          {rowsInLevel.map((row: any) => {
            return <div style={style(row)}>
              {(this.rowTitleOrientation === "vertical" && row.kind === "dimension") || (this.props.rowLabelOrientation === "vertical" && row.kind !== "dimension")
                ? <p style={verticalTextStyle(row.height * this.cellHeight)}>{row.displayName || row.key}</p>
                : row.displayName || row.key
              }
            </div>
          })}
        </div>
      })(groupBy("level")(this.rows))
    )
  }

  renderColumns() {
    return (
      this.columns.map((column: any) => {
        return <div style={column.kind === "dimension" ? columnTitleStyle(column.width * this.cellWidth) : columnLabelStyle(column.width * this.cellWidth)}>{column.displayName || column.key}</div>
      })
    )
  }

  renderVisualization(datum: any) {
    return <VisualizationWrapper
      facade={this.props.facade}
      data={datum}
      accessors={this.props.accessors}
      config={defaults(defaultVisualizationConfig(this.cellWidth - 2, this.cellHeight - 2))(this.props.config)}
    ></VisualizationWrapper>
  }

  renderCells() {
    return (
      this.props.data.cells.map((row: any) => {
        return row.map((cell: any) => {
          return <div style={cellStyle(this.props.displayCellBorders, this.cellWidth, this.cellHeight)}>
            {this.props.facade ? this.renderVisualization(cell) : cell}
          </div>
        })
      })
    )
  }

  render() {
    this.rowTitleOrientation = this.props.rowLabelOrientation === "vertical" ? "vertical" : (this.props.rowTitleOrientation || "horizontal")
    this.computeColumns()
    this.computeRows()
    this.computeCellDimensions()

    return (
      <div style={{ color: theme.font.color }}>
        <div style={{ fontWeight: 500, paddingTop: this.getColumnsHeight() - (this.rowTitleOrientation === "horizontal" ? lineHeight : 0), float: "left" }}>
          {this.renderRows()}
        </div>
        <div style={{ fontWeight: 500, width: this.getColumnsWidth(), marginLeft: this.getRowsWidth() }}>
          {this.renderColumns()}
        </div>
        <div style={{marginLeft: this.getRowsWidth(), width: this.getColumnsWidth()}}>
          {this.renderCells()}
        </div>
      </div>
    )
  }
}

export default GridLayout
