import { all, cloneDeep, compact, defaults, flatten, flow, forEach, includes, isArray, last, map, uniq, pluck, reduce, sortBy } from "lodash/fp"
import { zip } from "lodash"

type Datum = string | number | Date
// type SingleSeriesDatum = { [key: string]: Datum }
// type MultipleSeriesDatum = SingleSeriesDatum
// type ProcessedDatum = SingleSeriesDatum | MultipleSeriesDatum

type Dimension = any
type Measure = any
type Filter = any

interface NestedRow {
  key: string
  kind: string
  displayName?: string
  nestedRows?: NestedRow[]
}

interface NestedColumn {
  key: string
  kind: string
  displayName?: string
  nestedColumns?: NestedColumn[]
}

interface RawResult {
  cells: Datum[][]
  nestedRows: NestedRow[]
  nestedColumns: NestedColumn[]
}

interface ProcessedResult {
  cells: any[]
  nestedRows: NestedRow[]
  nestedColumns: NestedColumn[]
}

interface State {
  display: string
  rows: {
    dimensions: (Dimension | Measure[])[]
    y1: Dimension | Measure | Measure[]
    y2: Dimension | Measure | Measure[]
  }
  columns: {
    dimensions: (Dimension | Measure[])[]
    x1: Dimension | Measure | Measure[]
    x2: Dimension | Measure | Measure[]
  }
  filters: Filter[]
  series: {
    splitBy: (Dimension | Measure[])[]
    colors: {
      dimensions: (Dimension | Measure[])[]
      scale: string
      palette: string[]
      assigned: { key: string, color: string }[]
    }
    label: (Dimension | Measure)[]
    tooltip: (Dimension | Measure)[]
    renderAs: any[]
    size: {
      dimensions: (Dimension | Measure[])[]
      scale: string
      assigned: { key: string, size: string }[]
    }
  }
}

const removeNestedColumn = (dim: Dimension | Measure | Measure[], nested: NestedColumn[], dimValues: (Dimension | Measure[])[][] = []) => {
  const dimensionKeys = isArray(dim) ? pluck("key")(dim) : [dim.key]
  forEach((column: NestedColumn) => {
    if (!column.nestedColumns) { return }
    if (includes(column.nestedColumns[0].key)(dimensionKeys)) {
      dimValues.push(column.nestedColumns[0].kind === "dimension" ? column.nestedColumns[0].nestedColumns : column.nestedColumns)
      delete column.nestedColumns
    } else {
      removeNestedColumn(dim, column.nestedColumns, dimValues)
    }
  })(nested)
  return dimValues
}

const removeNestedRow = (dim: Dimension | Measure | Measure[], nested: NestedRow[], dimValues: (Dimension | Measure[])[][] = []) => {
  const dimensionKeys = isArray(dim) ? pluck("key")(dim) : [dim.key]
  forEach((row: NestedRow) => {
    if (!row.nestedRows) { return }
    if (includes(row.nestedRows[0].key)(dimensionKeys)) {
      dimValues.push(row.nestedRows[0].kind === "dimension" ? row.nestedRows[0].nestedRows : row.nestedRows)
      delete row.nestedRows
    } else {
      removeNestedRow(dim, row.nestedRows, dimValues)
    }
  })(nested)
  return dimValues
}

// Series splits are always the innermost nested columns.
const handleSeriesSplits = (seriesDimensions: any[]) => {
  return (result: RawResult) => {
    if (!seriesDimensions.length) { return result }
    let dimensionValues: any[] = []
    forEach((dim: any) => {
      const dimValues = removeNestedColumn(dim.key, result.nestedColumns)
      dimensionValues.push(dimValues)
      const newCells = map((row: any) => {
        const newRow = cloneDeep(dimValues)
        forEach.convert({ cap: false })((value: Dimension | Measure[], i: number) => {
          value.value = row[i]
        })(flatten(newRow))
        return map((cell: any) => { return { data: cell }})(newRow)
      })(result.cells)
      result.cells = newCells
    })(seriesDimensions)
    return dimensionValues
  }
}

const getTimeInterval = (val1: Date, val2: Date) => {
  if (val2.getFullYear() - val1.getFullYear() > 0) {
    return "year"
  }
  if (val2.getMonth() - val1.getMonth() === 3) {
    return "quarter"
  }
  if (val2.getMonth() - val1.getMonth() > 0) {
    return "month"
  }
  if (val2.getDate() - val1.getDate() >= 7) {
    return "week"
  }
  if (val2.getDate() - val1.getDate() > 0) {
    return "day"
  }
  return "hour"
}

const getTimeAxisInfo = (group: any) => {
  const values = sortBy((date: Date) => date.valueOf)(map((val: any) => val.key || val)(group))
  return {
    type: "time",
    start: values[0],
    end: last(values),
    interval: getTimeInterval(values[0], values[1]),
  }
}

const getQuantAxisInfo = (group: any) => {
  const values = map((val: any) => val.key || val)(group)
  return {
    type: "quant",
    start: Math.min(0, Math.min(...values)),
    end: Math.max(0, Math.max(...values)),
  }
}

const getCategoricalAxisInfo = (group: any) => {
  return {
    type: "categorical",
    values: pluck("key")(group),
  }
}

const getAxisInfo = (group: any) => {
  switch (typeof (group[0].key || group[0])) {
    case "object":
      return getTimeAxisInfo(group)
    case "number":
      return getQuantAxisInfo(group)
    default:
      return getCategoricalAxisInfo(group)
  }
}

const groupColumns = (result: ProcessedResult, dimension: Dimension | Measure | Measure[], axis: "x1" | "x2") => {
  const dimensionValues = removeNestedColumn(dimension, result.nestedColumns)
  const isMeasure = dimension.kind !== "dimension"

  const newCells = map((row: any[]) => {
    let i = 0
    return map((group: any) => {
      const data = map.convert({ cap: false })((val: any, j: number) => {
        if (typeof val === "number") {
          return { y: val, [axis[0]]: group[j].key }
        } else {
          return defaults(val)({ [axis[0]]: group[j].key })
        }
      })(row.slice(i, i + group.length))
      debugger
      const axes = { [axis]: getAxisInfo(group)}
      i = i + group.length
      return { axes, series: { data } }
    })(dimensionValues)
  })(result.cells)
  result.cells = newCells
}

const groupRows = (result: ProcessedResult, dimension: Dimension | Measure | Measure[], axis: "y1" | "y2") => {
  const dimensionValues = removeNestedRow(dimension, result.nestedRows)
  let i = 0
  const isDimension = dimension.kind === "dimension"
  const isGrouped = result.cells[0][0].axes

  // For each group of dimension values or measures...
  const newCells = map((group: any) => {
    // ...isolate rows that need to be grouped together...
    const rows = result.cells.slice(i, i + group.length)
    // ...get y axis information for the new, grouped row...
    const axisInfo = !isGrouped
      ? getAxisInfo(flatten(rows))
      : flow(
          map(pluck("series")),
          flatten,
          pluck("data"),
          flatten,
          map((val: any) => val.y),
          getAxisInfo
        )(rows)

    const newRow = map((cell: any) => {
      if (isMeasure) {
        const series = map.convert({ cap: false })((series: any, j: number) => {
          return {
            data: series.series.data,
            name: group[j].displayName || group[j].key,
            key: group[j].key
          }
        })(cell)
        return {
          series,
          axes: defaults(cell[0].axes)({ [axis]: axisInfo})
        }
      } else {
        const data = map.convert({ cap: false })((val: any, j: number) => {
          if (typeof val === "number") {
            return { x: val, [axis[0]]: group[j].key }
          } else {
            return defaults(val)({ [axis[0]]: group[j].key })
          }
        })(cell)
        return {
          axes: { [axis]: axisInfo },
          series: { data }
        }
      }
    })(zip(...rows))
    i = i + group.length
    return newRow
  })(dimensionValues)
  result.cells = newCells
}

const handleAxis = (state: State, result: ProcessedResult) => {
  return (axis: "x1" | "x2" | "y1" | "y2") => {
    const isColumn = axis[0] === "x"
    const dimension = state[isColumn ? "columns" : "rows"][axis]
    if (!dimension) { return }
    isColumn ? groupColumns(result, dimension, axis) : groupRows(result, dimension, axis)
  }
}

const handleAxes = (state: State, result: ProcessedResult) => {
  const axisDimensions = compact(flatten([state.columns.x2, state.columns.x1, state.rows.y2, state.rows.y1]))
  if (!axisDimensions.length) { return result }
  const baseAxisOrientation = all({ kind: "measure" })(compact(flatten([state.rows.y1, state.rows.y2]))) ? "x" : "y"
  const mainAxisOrientation = baseAxisOrientation === "x" ? "y" : "x"
  map(handleAxis(state, result))([`${baseAxisOrientation}2`, `${baseAxisOrientation}1`, `${mainAxisOrientation}2`, `${mainAxisOrientation}1`])
  return result
}

export default (result: RawResult, state: State) => {
  if (state.display === "table") { return result }
  handleSeriesSplits(flatten([state.series.splitBy, state.series.colors.dimensions]))(result)
  handleAxes(state, result)
  return result
}
