import * as React from "react"
import VisualizationWrapper from "./VisualizationWrapper"

import theme from "./constants"
import { cloneDeep, defaults, filter, flow, map, pluck, reduce } from "lodash/fp"

const columnWidth: number = 150
const lineHeight: number = 30

export interface Props {
  data?: any
  height?: number
  width?: number
  facade?: any
  config?: any
  accessors?: any
  rowLabelOrientation?: "horizontal" | "vertical"
  displayCellBorders?: boolean
}

const headerStyle = {
  lineHeight: `${lineHeight}px`,
  textAlign: "center",
  border: `1px solid ${theme.colors.axis.rules}`,
  overflow: "hidden",
  textOverflow: "ellipsis",
  padding: `${theme.space.small}px 0`
}

const titleStyle = {
  backgroundColor: theme.colors.background.lighter,
  ...headerStyle
}

const horizontalRowTitleStyle = {
  width: columnWidth,
  height: lineHeight,
  ...titleStyle
}

const verticalRowTitleStyle = (height: number) => {
  return {
    width: lineHeight,
    height: height,
    float: "left",
    ...titleStyle
  }
}

const columnTitleStyle = (width: number) => {
  return {
    width: width,
    height: lineHeight,
    ...titleStyle
  }
}

const labelStyle = {
  backgroundColor: theme.colors.white,
  ...headerStyle
}

const horizontalRowLabelStyle = (height: number) => {
  return {
    height: height,
    width: columnWidth,
    ...labelStyle,
    textAlign: "end",
    paddingRight: 8,
  }
}

const verticalRowLabelStyle = (height: number) => {
  return {
    height: height,
    width: lineHeight,
    ...labelStyle,
  }
}

const columnLabelStyle = (width: number) => {
  return {
    height: lineHeight,
    width: width,
    float: "left",
    ...labelStyle
  }
}

const verticalTextStyle = (height: number) => {
  return {
    width: height,
    height: lineHeight,
    transform: `rotate(-90deg) translate(${lineHeight / 2}px, -${height}px)`,
    transformOrigin: "right top",
    whiteSpace: "nowrap",
    overflow: "hidden",
    textOverflow: "ellipsis",
    padding: `0 ${theme.space.small}px`
  }
}

const cellStyle = (displayBorder: boolean, width: number, height: number) => {
  return {
    height: height,
    width: width,
    border: `1px solid ${displayBorder ? theme.colors.axis.rules : theme.colors.white}`,
    float: "left",
    lineHeight: `${lineHeight}px`,
    textAlign: "end",
    paddingRight: 8,
    backgroundColor: theme.colors.white,
  }
}

const defaultVisualizationConfig = (width: number, height: number) => {
  return {
    width,
    height,
    legend: false,
  }
}

class GridLayout extends React.Component<Props, {}> {
  cellHeight: number
  cellWidth: number

  public static defaultProps: Partial<Props> = {
    rowLabelOrientation: "horizontal",
    displayCellBorders: true,
  }

  computeCellDimensions() {
    this.cellHeight = this.props.height
      ? (this.props.height - this.getColumnsHeight()) / this.props.data.rows.length
      : lineHeight

    this.cellWidth = this.props.width
      ? (this.props.width - this.getRowsWidth()) / this.props.data.columns.length
      : columnWidth
  }

  getColumnsWidth() {
    return filter((column: any) => column.measure)(this.props.data.columns).length * this.cellWidth
  }

  getRowsWidth() {
    const nRowDimensions = filter((column: any) => !column.measure)(this.props.data.columns).length
    return nRowDimensions * (this.props.rowLabelOrientation === "horizontal" ? columnWidth : 2 * lineHeight)
  }

  getColumnsHeight() {
    const hasMultipleMeasures = this.props.data.measures.length > 1
    return (this.props.data.columnHeaders.length * 2 + (hasMultipleMeasures ? 1 : 0)) * lineHeight
  }

  computeLabels(headers: any, dimensions: any[]) {
    const cloned = cloneDeep(dimensions)
    return map((header: any) => {
      const labels = map((dimension: any[]) => dimension.shift())(cloned)
      header.count = labels.length
      return reduce((memo: { displayName: string, count: number }[], value: string) => {
        const previous = memo[memo.length - 1]
        if (previous && previous.displayName === value) { previous.count = previous.count + 1 } else { memo.push({ displayName: value, count: 1 })}
        return memo
      }, [])(labels)
    })(headers)
  }

  renderRows() {
    const rowDimensions = filter((column: any) => !column.measure)(this.props.data.columns)
    const rowLabels = this.computeLabels(rowDimensions, this.props.data.rows)
    const isHorizontal = this.props.rowLabelOrientation === "horizontal"
    const titleStyle = (row: any) => isHorizontal ? horizontalRowTitleStyle : verticalRowTitleStyle(row.count * this.cellHeight)
    const labelStyle = (row: any) => (isHorizontal ? horizontalRowLabelStyle : verticalRowLabelStyle)(row.count * this.cellHeight)
    const textInput = (row: any) => isHorizontal ? row.displayName : <p style={verticalTextStyle(row.count * this.cellHeight)}>{row.displayName}</p>
    return (
      rowDimensions.map((header: any, i: number) => {
        return <div style={{float: "left"}}>
          {<div style={titleStyle(header)}>{textInput(header)}</div>}
          {rowLabels[i].map((label: any) => {
            return <div style={labelStyle(label)}>{textInput(label)}</div>
          })}
        </div>
      })
    )
  }

  renderColumns() {
    const columns = filter((column: any) => column.measure)(this.props.data.columns)
    const columnLabels = this.computeLabels(columns, pluck("values")(columns))
    const measureLabels = this.props.data.measures.length > 1 ? pluck("measure")(columns) : undefined
    return (
      <>
        {this.props.data.columnHeaders.map((header: any, i: number) => {
          return <div style={{float: "left"}}>
            {<div style={columnTitleStyle(columns.length * this.cellWidth)}>{header.displayName || header.key }</div>}
            {columnLabels[i].map((label: any) => {
              return <div style={columnLabelStyle(label.count * this.cellWidth)}>{label.displayName || label.key }</div>
            })}
          </div>
        })}
        {measureLabels && <div style={{float: "left"}}>{measureLabels.map((label: string) => {
          return <div style={columnLabelStyle(this.cellWidth)}>{label}</div>
        })}</div>}
      </>
    )
  }

  renderVisualization(datum: any) {
    return <VisualizationWrapper
      facade={this.props.facade}
      data={datum}
      accessors={this.props.accessors}
      config={defaults(defaultVisualizationConfig(this.cellWidth - 2, this.cellHeight - 2))(this.props.config)}
    ></VisualizationWrapper>
  }

  renderCells() {
    const numberOfRowDimensions = filter((column: any) => !column.measure)(this.props.data.columns).length
    return (
      this.props.data.rows.map((row: any) => {
        return row.slice(numberOfRowDimensions, row.length).map((cell: any) => {
          return <div style={cellStyle(this.props.displayCellBorders, this.cellWidth, this.cellHeight)}>
            {this.props.facade ? this.renderVisualization(cell) : cell}
          </div>
        })
      })
    )
  }

  render() {
    this.computeCellDimensions()

    return (
      <div style={{ color: theme.font.color }}>
        <div style={{ fontWeight: 500, paddingTop: this.getColumnsHeight() - (this.props.rowLabelOrientation === "horizontal" ? lineHeight : 0), float: "left" }}>
          {this.renderRows()}
        </div>
        <div style={{ fontWeight: 500, width: this.getColumnsWidth(), marginLeft: this.getRowsWidth() }}>
          {this.renderColumns()}
        </div>
        <div style={{marginLeft: this.getRowsWidth(), width: this.getColumnsWidth()}}>
          {this.renderCells()}
        </div>
      </div>
    )
  }
}

export default GridLayout
